    display:flex;
   -ms-flex-direction: column;
       flex-direction: column;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 3em;
}


.buttonForm{
    min-width: 250px;
    font-family: 'Montserrat', sans-serif;
    font-weight: bold;
    padding: 0.4em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}

.answerButton{
    text-align: center;
    margin: 0.5em;
    padding: 0.5em;
    background-color: papayawhip;
    -webkit-transition: 0.3s all ease;
    -o-transition: 0.3s all ease;
    transition: 0.3s all ease;
    border: none;
}
.answerButton:hover{
    opacity: 0.6;
}


